.container {
  padding: 16px 0;
  padding-right: 16px;
  border-bottom: 1px solid #d9d9d9;
}

.header {
  display: flex;
  justify-content: space-between;
  margin: 0;
}

.date {
  font-size: 12px;
  line-height: 13px;
  text-align: right;
  color: #a79595;
}

.message {
  margin: 0;
  line-height: 13px;
  word-wrap: break-word;
}
