.list {
  display: flex;
  justify-content: space-between;
  width: 136px;
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 17px;
  margin-bottom: 8px;
}

.listInsideForm {
  width: 196px;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 16px;
}

.listInsideForm .star {
  cursor: pointer;
}
.active {
  fill: #f56a3f;
}

.nonActive {
  fill: #c4c4c4;
}
